<!--

Modal Component
by Jean-Francois Desrochers

Attributes:
- isOpen:       True is the modal is currently open, false otherwise. Do not set
                this attribute to control the modal's state. Use open and close
                instead. See below.
- title:        The modal's title.
- mandatory:    If set to true, this will remove the 'X' button and prevent the user
                from canceling the modal by clicking on the backdrop.
- centered:     Set to true if you want the modal to be centered in the page.
- actions:      Pass an array of objects to specify the action buttons in the modal's
                footer. They will follow the array's order, from left to right. Each
                object in the array can have the following properties:
  - label:          The button's caption
  - fn:             The function that will be called for the OnClick event.
  - intent:         Sets the color of the button (default, primary, success, warning, danger).
  - outline:        Sets the visual style of the button as outlined instead of filled.
  - disabled:       Set to true to disable the button.
  - loading:        Set to true to indicate a loading state.
  - loadingMessage: Customize the loading message is loading is true.

Methods:
- open:         Opens the modal.
- close:        Closes the modal.
- toggle:       Toggles the modal between closed and open and vice-versa.

To use the methods: bind 'this' to a variable of your choice and then call the methods on this variable:
    <script>
        let modal;
    </script>

    <Modal bind:this={modal}/>
    <button on:click={() => modal.open()}>Open modal</button>

Events:
- opening:      Triggered as soon as the 'open' method is called, before the modal is
                fully visible.
- open:         Triggered when the modal's transition is complete and it is fully visible.
- closing:      Triggered as soon as the 'close' method is called, before the modal is
                fully closed.
- closed:       Triggered when the modal's transition is complete and it is fully closed.

-->
<script>
    import { fade, fly } from "svelte/transition";
    import { createEventDispatcher } from "svelte";

    import Button from "./Button.svelte";

    const dispatch = createEventDispatcher();

    export let isOpen = false;
    export let title = "";
    export let mandatory = false;
    export let centered = false;
    export let actions = [];

    export const open = () => {
        isOpen = true;
        dispatch("opening");
    };
    export const close = () => {
        isOpen = false;
        dispatch("closing");
    };
    export const toggle = () => {
        isOpen = !isOpen;
        if (isOpen) dispatch("opening");
        else dispatch("closing");
    };

    const onOpened = () => {
        dispatch("open");
    };

    const onClosed = () => {
        dispatch("closed");
    };
</script>

{#if isOpen}
    <!--svelte-ignore a11y-click-events-have-key-events-->
    <div
        class="modal-backdrop"
        class:centered
        transition:fade={{ duration: 300 }}
        tabindex="-1"
        on:click|self={() => !mandatory && close()}
    >
        <div
            class="modal"
            transition:fly={{ y: -50, duration: 300 }}
            on:introend={onOpened}
            on:outroend={onClosed}
        >
            <div class="modal-header">
                <div class="modal-title">
                    {title}
                </div>
                {#if !mandatory}
                    <button class="close" on:click={close}>
                        <span>&times;</span>
                    </button>
                {/if}
            </div>
            <div class="modal-body">
                <slot />
            </div>
            {#if actions.length}
                <div class="modal-footer">
                    {#each actions as action}
                        <Button
                            on:click={action.fn}
                            intent={action.intent || 'default'}
                            loading={action.loading}
                            loadingMessage={action.loadingMessage || 'Loading...'}
                            disabled={action.disabled}
                            outline={action.outline}
                        >{action.label}</Button>
                    {/each}
                </div>
            {/if}
        </div>
    </div>
{/if}

<style>
    .modal-backdrop {
        position: fixed;
        background-color: rgba(0, 0, 0, 0.1);
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        overflow-x: hidden;
        overflow-y: auto;
        outline: 0;
        z-index: 5000;
    }

    .modal {
        position: relative;
        background-color: var(--white);
        color: var(--black);
        border-radius: 0.5rem;
        display: flex;
        flex-direction: column;
        margin: 1.75rem auto;
        width: calc(100% - 1rem);
        max-width: 32rem;
        box-shadow: 0 4px 16px rgb(0, 0, 0, 0.2);
    }

    .centered {
        display: flex;
        align-items: center;
    }

    .modal-header {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        padding: 1rem;
        border-bottom: solid 1px #e9ecef;
    }

    .modal-title {
        font-weight: 500;
        line-height: 1.5;
        font-size: 1.25rem;
    }

    .modal-body {
        position: relative;
        flex: 1 1 auto;
        padding: 1rem;
    }

    .modal-footer {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        gap: 0.5rem;
        padding: 1rem;
        border-top: solid 1px #e9ecef;
    }

    .modal-header,
    .modal-footer {
        user-select: none;
    }

    .close {
        padding: 1rem;
        margin: -1rem -1rem -1rem auto;
        background-color: transparent;
        border: 0;
        cursor: pointer;
        font-size: 1.5rem;
        font-weight: 700;
        line-height: 1;
        color: var(--black);
        text-shadow: 0 1px 0 var(--white);
        opacity: 0.5;
    }

    .close:focus,
    .close:hover {
        color: #000;
        text-decoration: none;
        opacity: 0.75;
    }

    .close:active {
        background-color: transparent;
    }
</style>
