<script>
    import { game, toasterDelayShort, toasterDelayLong } from "./stores.js";
    import { fade } from "svelte/transition";

    let toasterTimeout = null;
    let lastMessage = $game.message;

    function dismissToaster() {
        $game.message = "";
        lastMessage = "";
        toasterTimeout = null;
    }

    $: if ($game.message !== "" && $game.message !== lastMessage) {
        if (toasterTimeout) clearTimeout(toasterTimeout);
        if ($game.ended) {
            if ($game.win) {
                toasterTimeout = setTimeout(dismissToaster, toasterDelayLong);
            }
        } else {
            toasterTimeout = setTimeout(dismissToaster, toasterDelayShort);
        }
        lastMessage = $game.message;
    }
</script>

<div class="toaster" id="game-toaster">
    {#if $game.message}
        <div
            class="toast"
            class:win={$game.win}
            transition:fade={{ duration: 300 }}
        >
            {$game.message}
        </div>
    {/if}
</div>

<style>
    #game-toaster {
        z-index: 1000;
    }

    .toaster {
        position: absolute;
        top: 10%;
        left: 50%;
        transform: translate(-50%, 0);
        pointer-events: none;
        width: fit-content;
    }

    .toast {
        position: relative;
        margin: 16px;
        background-color: var(--color-tone-1);
        color: var(--color-tone-7);
        padding: 16px;
        border: none;
        border-radius: 4px;
        font-weight: 700;
    }

    .win {
        background-color: var(--color-correct);
        color: var(--tile-text-color);
    }
</style>
