<script>
    import { fade } from "svelte/transition";

    export let list = [];
    export let loading = false;
</script>

<ul>
    {#if loading}
        <div>Loading...</div>
    {:else if list.length > 0}
        {#each list as item}
            <li transition:fade={{ duration: 300 }}>
                <details>
                    <summary>
                        <div class="name">{item.name}</div>
                        <div class="score" class:great={item.numHints === 1 || item.numHints === 2} class:good={item.numHints === 3 || item.numHints === 4}>{item.numHints}</div>
                    </summary>
                    <div class="board"><pre>{item.board}</pre></div>
                </details>
            </li>
        {/each}
    {:else}
        <div>Nothing to see</div>
    {/if}
</ul>

