<script>
    import { enterLetter, evaluateGame, game, konami, leaderboard, statusToEmoji } from "./stores.js";
    import Board from "./Board.svelte";
    import Keyboard from "./Keyboard.svelte";
    import Toaster from "./Toaster.svelte";
    import Leaderboard from "./Leaderboard.svelte";
    import Modal from "./Modal.svelte";
    import { onMount } from "svelte";

    let welcomeModal;
    let winModal;
    let winName = '';
    let winActions = [{
        label: 'Submit',
        intent: 'primary',
        loadingMessage: 'Submitting...',
        loading: false,
        disabled: true,
        fn: async () => {
            try {
                winActions[0].loading = true;
                await leaderboard.setLeaderboard($game.wordNumber, winName, guessBoard, guessAnswers, numGuesses);
            } finally {
                winActions[0].loading = false;
                winModal.close();
            }
        }
    }];

    $: winActions[0].disabled = winName.length === 0;

    const leaderboardStore = leaderboard.store;

    let yesterdaysLb = [];
    let yesterdaysLoading = false;

    let numGuesses = 0;
    let guessBoard = '';
    let guessAnswers = '';

    function handleKeydown(evt) {
        if (evt.target.tagName === 'INPUT') return;
        evt.preventDefault();
        evt.stopPropagation();
        konami.checkKey(evt.code);
        let key = evt.key.toUpperCase();
        if (key === "ENTER") {
            evaluateGame();
        } else if (key === "BACKSPACE") {
            enterLetter("0");
        } else if (key.length === 1) {
            let code = key.charCodeAt(0);
            if (code >= 65 && code <= 90) {
                enterLetter(key);
            }
        }
    }

    $: if ($game.ended && $game.win && $game.message !== '') {
        numGuesses = 0;
        guessBoard = '';
        guessAnswers = '';
        for (let row of $game.rows) {
            if (row.letters.length > 0) {
                if (guessBoard.length > 0) guessBoard += '\n';
                if (guessAnswers.length > 0) guessAnswers += '\n';
                numGuesses++;
                for (let evaluation of row.evaluations) {
                    guessBoard += statusToEmoji[evaluation];
                }
                guessAnswers += row.letters;
            }
        }
        numGuesses -= 3;
        winModal.open();
    }

    onMount(async () => {
        if ($game.rows[3].letters.length === 0) {
            welcomeModal.open();
        }
        try {
            yesterdaysLoading = true;
            if ($game.wordNumber > 1)
                yesterdaysLb = await leaderboard.getLeaderboard($game.wordNumber - 1);
        } finally {
            yesterdaysLoading = false;
        }
    });
</script>

<svelte:window on:keydown={handleKeydown} />

<header>
    <div class="menu-left" />
    <div class="title">NHBordle Overtime</div>
    <div class="menu-right" />
</header>
<div class="game-container">
    <div id="game">
        <div id="board-container">
            <Board />
        </div>
        <Keyboard />
        <Toaster />
    </div>
    <div id="leaderboard">
        <h3>Leaderboard</h3>
        <Leaderboard list={$leaderboardStore.list} loading={$leaderboardStore.loading} />
    </div>
</div>

<Modal bind:this={welcomeModal} title="Welcome to NHBordle Overtime #{$game.wordNumber}!">
    <h3 class="mt-0">Rules</h3>
    <p>This game plays exactly like <a href="https://jfdesrochers.github.io/kmhbordle" target="_blank">NHBordle</a> with the following exceptions:</p>
    <ul>
        <li>The game will provide you with 3 guesses already filled.</li>
        <li>There will be 3 guaranteed clues in these guesses.</li>
        <li>You will have to guess the word using these clues.</li>
        <li>As such, you will have only 3 remaining guesses to do so. Choose carefully!</li>
    </ul>
    <h3>Yesterday's Leaderboard #{$game.wordNumber - 1}</h3>
    <Leaderboard list={yesterdaysLb} loading={yesterdaysLoading} />
</Modal>

<Modal bind:this={winModal} title="Congratulations!" actions={winActions} mandatory>
    <h3 class="mt-0">You WIN in {numGuesses} {numGuesses > 1 ? 'guesses' : 'guess'}!</h3>
    <pre>{guessBoard}</pre>
    <label for="leadername">Please enter your name below for the leaderboard:</label>
    <input type="text" name="leadername" id="leadername" bind:value={winName}>
</Modal>

