        gap: 1rem;
    }

    #game {
        width: 100%;
        max-width: var(--game-max-width);
        height: calc(100% - var(--header-height));
        display: flex;
        flex-direction: column;
    }

    #leaderboard {
        width: 100%;
        min-height: 300px;
        max-width: 300px;
        height: calc(100% - var(--header-height) - 2rem);
        overflow: auto;
        background-color: var(--leaderboard-bg);
        padding: 1rem;
        margin: 1rem 0;
        border-radius: 1rem;
    }

    #leaderboard h3 {
        margin: 0;
        margin-bottom: 1rem;
        text-align: center;
    }

    #board-container {
        display: flex;
