        align-items: center;
        flex-grow: 1;
        overflow: hidden;
    }

    .mt-0 {
        margin-top: 0;
    }

    label {
        display: block;
        margin-bottom: 0.5rem;
        font-weight: bold;
    }

    input {
        position: relative;
        font-weight: 400;
        font-size: 1rem;
        line-height: 1.5;
        padding: .35rem .75rem;
        border-radius: .5rem;
        outline: none;
        border: solid 1px transparent;
        background-color: var(--gray-100);
        width: 100%;
        transition: box-shadow 0.15s ease-in-out;
    }

    input:focus {
