<!--

Button Component
by Jean-Francois Desrochers

Attributes:
- intent:         Defined the button's color. You have the following choices:
                    * default:        Gray
                    * primary:        Blue
                    * primary-indigo: Indigo
                    * primary-purple: Purple
                    * primary-pink:   Pink
                    * success:        Green
                    * warning:        Yellow/Orange
                    * danger:         red
- disabled:       Set to true to disable the button.
- small:          Set to true to render a smaller-sized button.
- outline:        Show only the outline of the button.
- loading:        Set to true to show the button in a loading state.
- loadingMessage: Message to display during the loading state.

Events:
- click:          Forwarded from the DOM Event of the same name.

-->
<script>
    export let intent = 'default';
    export let disabled = false;
    export let small = false;
    export let outline = false;
    export let loading = false;
    export let loadingMessage = 'Loading...';
</script>

<button on:click class={intent} class:small class:outline disabled={disabled || loading}>
    {#if loading}
        {loadingMessage}
    {:else}
        <slot/>
    {/if}
</button>

