import { writable } from "svelte/store";
import { kmhbWords, allWords } from "./wordlists.js";
import PocketBase from "pocketbase";

// Returns a random function that always gives the same values given the same seed phrase.
function seededRandom(seed) {
	return sfc32(...cyrb128(seed));
}

/*--
Source: https://stackoverflow.com/a/47593316
--*/

function cyrb128(str) {
    let h1 = 1779033703, h2 = 3144134277,
        h3 = 1013904242, h4 = 2773480762;
    for (let i = 0, k; i < str.length; i++) {
        k = str.charCodeAt(i);
        h1 = h2 ^ Math.imul(h1 ^ k, 597399067);
        h2 = h3 ^ Math.imul(h2 ^ k, 2869860233);
        h3 = h4 ^ Math.imul(h3 ^ k, 951274213);
        h4 = h1 ^ Math.imul(h4 ^ k, 2716044179);
    }
    h1 = Math.imul(h3 ^ (h1 >>> 18), 597399067);
    h2 = Math.imul(h4 ^ (h2 >>> 22), 2869860233);
    h3 = Math.imul(h1 ^ (h3 >>> 17), 951274213);
    h4 = Math.imul(h2 ^ (h4 >>> 19), 2716044179);
    return [(h1^h2^h3^h4)>>>0, (h2^h1)>>>0, (h3^h1)>>>0, (h4^h1)>>>0];
}

function sfc32(a, b, c, d) {
    return function() {
      a >>>= 0; b >>>= 0; c >>>= 0; d >>>= 0; 
      var t = (a + b) | 0;
      a = b ^ b >>> 9;
      b = c + (c << 3) | 0;
      c = (c << 21 | c >>> 11);
      d = d + 1 | 0;
      t = t + d | 0;
      c = c + t | 0;
      return (t >>> 0) / 4294967296;
    }
}

window.addEventListener('hashchange', () => {
    location.reload();
});

class Konami {
    #check = 'UUDDLRLRBAE';
    #code = '';

    checkKey(keyCode) {
        switch (keyCode) {
            case 'ArrowUp':
                this.#code += 'U';
            break;
            case 'ArrowDown':
                this.#code += 'D';
            break;
            case 'ArrowLeft':
                this.#code += 'L';
            break;
            case 'ArrowRight':
                this.#code += 'R';
            break;
            case 'KeyB':
                this.#code += 'B';
            break;
            case 'KeyA':
                this.#code += 'A';
            break;
            case 'Enter':
                this.#code += 'E';
            break;
        }
        if (!this.#check.startsWith(this.#code)) this.#code = '';
        if (this.#check === this.#code) {
            localStorage.removeItem('NHBordleOvertime.state');
            this.#code = '';
            location.reload();
        }
    }
}

export const konami = new Konami();

class LeaderboardData {
    #pb;
    isConnected = false;
    store = writable({list: [], loading: false});

    constructor() {
        this.#pb = new PocketBase('https://api.nhb.tools');
    }

    async connect(gameNumber) {
        try {
            this.store.update(state => {state.loading = true; return state});
            await this.#pb.collection('nbovertime_leaderboard').subscribe('*', (e) => {
                this.store.update((state) => {
                    let idx;
                    switch (e.action) {
                        case 'delete':
                            idx = state.list.findIndex(v => v.id === e.record.id);
                            if (idx > -1) state.list.splice(idx, 1)
                            break;
                        case 'update':
                            idx = state.list.findIndex(v => v.id === e.record.id);
                            if (idx > -1) state.list[idx] = e.record;
                            break;
                        default:
                            state.list.push(e.record);
                    }
                    state.list.sort((a, b) => a.numHints - b.numHints);
                    return state;
                })
            }, {filter: `gameNumber = ${gameNumber}`});
            await this.initLeaderboard(gameNumber);
            this.isConnected = true;
        } catch (err) {
            console.error(err);
            this.isConnected = false;
        } finally {
            this.store.update(state => {state.loading = false; return state});
        }
    }

    async disconnect() {
        try {
            this.store.update(state => {state.loading = true; return state});
            await this.#pb.collection('nbovertime_leaderboard').unsubscribe();
            this.isConnected = false;
        } catch (err) {
            console.error(err);
            this.isConnected = false;
        } finally {
            this.store.update(state => {state.loading = false; return state});
        }
    }

    async getLeaderboard(gameNumber) {
        let leaderboard = [];
        try {
            if (!this.#pb) return leaderboard;
            leaderboard = await this.#pb.collection('nbovertime_leaderboard').getFullList({
                sort: 'numHints',
                filter: `gameNumber = ${gameNumber}`
            });
        } catch (err) {
            console.error(err);
        } finally {
            return leaderboard;
        }
    }

    async setLeaderboard(gameNumber, name, board, boardAnswers, numHints) {
        try {
            await this.#pb.collection('nbovertime_leaderboard').create({
                gameNumber, name, board, boardAnswers, numHints
            });
        } catch (err) {
            console.error(err);
        }
    }

    async initLeaderboard(gameNumber) {
        try {
            this.store.update(state => {state.loading = true; return state});
            const list = await this.getLeaderboard(gameNumber);
            this.store.update(state => {
                state.list = list;
                return state;
            });
        } catch (err) {
            console.error(err);
        } finally {
            this.store.update(state => {state.loading = false; return state});
        }
    }
}

export const rowCount = 6;
export const letterCount = 5;
export const statusCorrect = 'correct';
export const statusPresent = 'present';
export const statusAbsent = 'absent';
export const statusTbd = 'tbd';
export const statusEmpty = 'empty';
export const toasterDelayShort = 1500;
export const toasterDelayLong = 3000;
export const statusToEmoji = {
    'correct': '🟩',
    'present': '🟨',
    'absent': '⬛',
    'tbd': '⬛',
    'empty': '🔳'
}

const congratulations = ['Genius', 'Magnificent', 'Impressive', 'Splendid', 'Great', 'Phew'];
const gameStart = new Date(1740700000000);
function daysDiff(d1, d2) {
    const typicalDay = 24 * 60 * 60 * 1000; // Typical day: excluding those pesky DST days.
    d1.setHours(0, 0, 0, 0);
    d2.setHours(0, 0, 0, 0);
    return Math.round((d1 - d2) / typicalDay);
}
let wordIndex = daysDiff(new Date(), gameStart);

if (location.hash && location.hash.startsWith('#/') && location.hash.length > 2 && parseInt(location.hash.slice(2)) !== NaN) {
    wordIndex = Math.abs(parseInt(location.hash.slice(2)) - 1);
}

while (wordIndex > kmhbWords.length) {
    wordIndex -= kmhbWords.length;
}
const wordNumber = wordIndex + 1;
document.title = "NHBordle Overtime #" + wordNumber;
const todaysWord = kmhbWords[wordIndex];

export function createGameStore(loaddata) {
    if (loaddata) return writable(loaddata);
    let rows = new Array(rowCount);
    for (let i = 0; i < rowCount; i++) {
        rows[i] = { win: false, invalid: false, letters: '', evaluations: new Array(letterCount).fill(statusEmpty) };
    }
    return writable({ wordNumber, rows, keyboard: {}, win: false, ended: false, message: '', lastPlay: (new Date()).getTime() });
}

let savedGame = JSON.parse(localStorage.getItem('NHBordleOvertime.state'));
if (savedGame && (savedGame.wordNumber !== wordNumber)) {
    savedGame = null;
}

export const game = createGameStore(savedGame);
export const leaderboard = new LeaderboardData();
leaderboard.connect(wordNumber);

const random = seededRandom(todaysWord);
const prefilledGuesses = [];
const foundClues = new Set();
while (prefilledGuesses.length < 3) {
    const word = allWords[Math.floor(random() * allWords.length)];
    let clueCount = 0;
    let lastClue = '';
    for (let i = 0; i < word.length; i++) {
        if (todaysWord.indexOf(word[i]) > -1) {
            clueCount++;
            lastClue = word[i];
        }
    }
    if (clueCount === 1 && !foundClues.has(lastClue)) {
        prefilledGuesses.push(word);
        foundClues.add(lastClue);
    }
}

for (let i = 0; i < prefilledGuesses.length; i++) {
    game.update((state) => {
        state.rows[i].letters = prefilledGuesses[i];
        state.rows[i].evaluations.fill(statusTbd);
        return state;
    });
    evaluateGame();
}

export function enterLetter(letter) {
    game.update((state) => {
        if (state.ended) return state;
        for (let row of state.rows) {
            if (row.evaluations[0] === statusEmpty || row.evaluations[0] === statusTbd) {
                if (letter === '0') {
                    if (row.letters.length > 0) {
                        row.evaluations[row.letters.length - 1] = statusEmpty;
                        row.letters = row.letters.slice(0, row.letters.length - 1);
                    }
                } else if (row.letters.length < letterCount) {
                    row.letters += letter;
                    row.evaluations[row.letters.length - 1] = statusTbd;
                }
                break;
            }
        }
        return state;
    });
}

export function evaluateGame() {
    game.update((state) => {
        for (let i = 0; i < state.rows.length; i++) {
            if (state.rows[i].evaluations[0] === statusTbd) {
                if (state.rows[i].letters.length < letterCount) {
                    state.message = 'Not enough letters';
                    state.rows[i].invalid = true;
                } else if (kmhbWords.indexOf(state.rows[i].letters) === -1 && allWords.indexOf(state.rows[i].letters) === -1) {
                    state.message = 'Not in word lists';
                    state.rows[i].invalid = true;
                } else {
                    // Perform evaluation
                    let correctCount = 0;
                    // Every possible letters that are not 'confirmed'
                    let looseLetters = todaysWord.split('').filter((letter, idx) => state.rows[i].letters[idx] !== letter);
                    for (let j = 0; j < state.rows[i].letters.length; j++) {
                        const letter = state.rows[i].letters[j];
                        const looseIdx = looseLetters.indexOf(letter);
                        if (letter === todaysWord[j]) {
                            state.rows[i].evaluations[j] = statusCorrect;
                            state.keyboard[letter] = statusCorrect;
                            correctCount++;
                        } else if (looseIdx > -1) {
                            state.rows[i].evaluations[j] = statusPresent;
                            if (state.keyboard[letter] !== statusCorrect) state.keyboard[letter] = statusPresent;
                            // 'Consume' the letter so it doesn't trigger again
                            looseLetters[looseIdx] = '';
                        } else {
                            state.rows[i].evaluations[j] = statusAbsent;
                            if (!state.keyboard.hasOwnProperty(letter)) {
                                state.keyboard[letter] = statusAbsent;
                            }
                        }
                    }
                    if (correctCount === letterCount) {
                        state.rows[i].win = true;
                        state.win = true;
                        state.ended = true;
                        state.message = congratulations[i];
                    } else if (i === rowCount - 1) {
                        state.ended = true;
                        state.message = todaysWord;
                    }
                    state.lastPlay = (new Date()).getTime();
                    localStorage.setItem('NHBordleOvertime.state', JSON.stringify({ ...state, message: '' }));
                }
                break;
            }
        }
        return state;
    });
}

export function resetInvalid() {
    game.update((state) => {
        for (let row of state.rows) {
            row.invalid = false;
        }
        return state;
    });
}