<script>
    import { statusEmpty, statusTbd } from "./stores.js";

    export let letter = "";
    export let letterIndex = 0;
    export let evaluation = statusEmpty;

    let previousLetter = letter;
    let previousEvaluation = evaluation;
    let animationState = "idle";

    $: {
        if (letter !== previousLetter) {
            animationState = "pop";
            previousLetter = letter;
        }
        if (evaluation !== previousEvaluation) {
            if (evaluation !== statusEmpty && evaluation !== statusTbd) {
                if (letterIndex > 0)
                    setTimeout(
                        () => (animationState = "flip-in"),
                        25 * letterIndex
                    );
                else {
                    animationState = "flip-in";
                }
            }
            previousEvaluation = evaluation;
        }
    }

    function resetAnimation() {
        if (animationState === "flip-in") animationState = "flip-out";
        else animationState = "idle";
    }
</script>

<section>
    <div
        class={`tile ${evaluation || statusEmpty}`}
        data-animation={animationState}
        on:animationend={resetAnimation}
    >
        {letter}
    </div>
</section>

<style>
    section {
        display: inline-block;
    }

    .tile {
        width: 100%;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        font-size: 2rem;
        line-height: 2rem;
        font-weight: bold;
        vertical-align: middle;
        box-sizing: border-box;
        color: var(--tile-text-color);
        text-transform: uppercase;
        user-select: none;
    }

    .tile::before {
        content: "";
        display: inline-block;
        padding-bottom: 100%;
    }

    /* Allow tiles to be smaller on small screens */
    @media (max-height: 600px) {
        .tile {
            font-size: 1em;
            line-height: 1em;
        }
    }

    .tile.empty {
        border: 2px solid var(--color-tone-4);
    }

    .tile.tbd {
        background-color: var(--color-tone-7);
        border: 2px solid var(--color-tone-3);
        color: var(--color-tone-1);
    }

    .tile.correct {
        background-color: var(--color-correct);
    }

    .tile.present {
        background-color: var(--color-present);
    }

    .tile.absent {
        background-color: var(--color-absent);
    }

    .tile[data-animation="pop"] {
        animation-name: PopIn;
        animation-duration: 100ms;
    }

    .tile[data-animation="flip-in"] {
        animation-name: FlipIn;
        animation-duration: 250ms;
        animation-timing-function: ease-in;
    }

    .tile[data-animation="flip-out"] {
        animation-name: FlipOut;
        animation-duration: 250ms;
        animation-timing-function: ease-in;
    }

    @keyframes PopIn {
        from {
            transform: scale(0.8);
            opacity: 0;
        }

        40% {
            transform: scale(1.1);
            opacity: 1;
        }
    }
    @keyframes FlipIn {
        0% {
            transform: rotateX(0);
        }
        100% {
            transform: rotateX(-90deg);
        }
    }
    @keyframes FlipOut {
        0% {
            transform: rotateX(-90deg);
        }
        100% {
            transform: rotateX(0);
        }
    }
</style>
