<script>
    import { enterLetter, evaluateGame, game } from "./stores.js";

    const keys = ["QWERTYUIOP", " ASDFGHJKL ", "1ZXCVBNM0"];

    function handleKey(evt) {
        let key = evt.target.dataset.key;
        if (key !== "1") {
            enterLetter(key);
        } else {
            evaluateGame();
        }
    }
</script>

<div id="keyboard">
    {#each keys as row}
        <div class="row">
            {#each row as key}
                {#if key === " "}
                    <div class="half" />
                {:else}
                    <button
                        class:one-and-a-half={key === "1" || key === "0"}
                        class={$game.keyboard[key] || ""}
                        data-key={key}
                        on:click={handleKey}
                        >{key === "1"
                            ? "ENTER"
                            : key === "0"
                            ? "BKSP"
                            : key}</button
                    >
                {/if}
            {/each}
        </div>
    {/each}
</div>

<style>
    #keyboard {
        margin: 0 8px;
        user-select: none;
        height: var(--keyboard-height);
    }

    .row {
        display: flex;
        width: 100%;
        margin: 0 auto 8px;
        touch-action: manipulation;
    }

    button {
        font-family: inherit;
        font-weight: bold;
        border: 0;
        padding: 0;
        margin: 0 6px 0 0;
        height: 58px;
        border-radius: 4px;
        cursor: pointer;
        user-select: none;
        background-color: var(--key-bg);
        color: var(--key-text-color);
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        text-transform: uppercase;
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0.3);
    }

    button:focus {
        outline: none;
    }

    button.fade {
        transition: background-color 0.1s ease, color 0.1s ease;
    }

    button:last-of-type {
        margin: 0;
    }

    .half {
        flex: 0.5;
    }

    .one-and-a-half {
        flex: 1.5;
        font-size: 12px;
    }

    button.correct {
        background-color: var(--key-bg-correct);
        color: var(--key-evaluated-text-color);
    }

    button.present {
        background-color: var(--key-bg-present);
        color: var(--key-evaluated-text-color);
    }

    button.absent {
        background-color: var(--key-bg-absent);
        color: var(--key-evaluated-text-color);
    }
</style>
